
$color_1: white;
$color_2: #95C857;
$color_3: black;
$color_4: #FFF;
$font_family_1: 'Roboto','Open Sans';
$font_family_2: 'Open Sans' , 'Arial' , sans-serif;
$background_color_1: #293037;
$background_color_2: #fff;

@font-face {
	font-family: 'roboto';
	src: url('../font/roboto/roboto.eot'); /* IE9 Compat Modes */
	src: url('../font/roboto/roboto.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
	url('../font/roboto/roboto.woff') format('woff'), /* Modern Browsers */
	url('../font/roboto/roboto.ttf')  format('truetype'), /* Safari, Android, iOS */
	url('../font/roboto/roboto.svg#svgFontName') format('svg'); /* Legacy iOS */
  }
  
  /* use the font */
  body {
	font-family: 'roboto', sans-serif;
  }

.head-container {
	height: 70px;
	background-image: url(../img/bg_header.png);
}
.logo {
	padding-left: 10px;
	font-size: 1.2em;
	color: $color_1;
	line-height: 70px;
	font-family: $font_family_1;
}
.title {
	text-align: center;
	font-size: 1.6em;
	font-weight: bold;
	color: $color_1;
	line-height: 70px;
	font-family: $font_family_1;
}
.status {
	text-align: right;
	font-size: 1.2em;
	color: $color_1;
	line-height: 70px;
	font-family: $font_family_1;
	i {
		color: $color_3;
	}
}
.user-link {
	padding-left: 10px;
	color: $color_2;
}
body {
	background: url(../img/bg_content.png) 0 0 repeat-x transparent;
	height: 100vh;
}
.btn2-unify {
	background: linear-gradient(0deg, rgb(93, 164, 7) 0%, rgb(152, 198, 61) 100%);
	color: $color_3 !important;
	margin-left: 5px !important;
	height: 25px;
	font-size: .8em !important;
	line-height: 25px;
	&:hover {
		text-decoration: underline;
		color: $color_4 !important;
	}
}
.nav-unify {
	text-align: center;
	top: 70px;
	font-family: $font_family_2;
	font-weight: 400;
	position: absolute;
	background: url(../img/bg_navi.png) 0 0 repeat-x transparent;
	width: 100%;
	color: $color_2;
	height: 40px;
}
.footer-unify {
	height: 70px;
	bottom: 0px;
	position: fixed;
	width: 100%;
	background-color: $background_color_1;
}
.unify-text {
	font-size: 13px;
	color: $color_4;
}
.main-container {
	height: auto;
	padding-top: 20px;
	width: 320px;
	background-color: $background_color_2;
	text-align: center;
}
#formLogon {
	width: 320px !important;
	text-align: center;
}
.input-field {
	margin: 0;
}
.logon-container {
	text-align: center;
}
.card-panel {
	text-align: center;
	width: 380px;
	margin: 0 auto;
}